import { AxiosError } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router-dom'

import { Card } from '@mui/material'

import { CONFERENCE_MESSAGE } from 'src/data/messages'

import { deleteConference, getConferences } from 'src/api/conference'

import type { Conference } from 'src/models/Conference'
import { type DateProps } from 'src/models/date'

import ConferencesTable from 'src/content/manage/Conference/ConferencesTable'

type ConferenceListProps = DateProps

const ConferenceList: React.FC<ConferenceListProps> = date => {
  const { id } = useParams()
  const clubId = Number(id)

  const { enqueueSnackbar } = useSnackbar()
  const authHeader = useAuthHeader()

  const [conferences, setConferences] = useState<Conference[]>([])

  const handleDelete = async (conferenceIds: number[]) => {
    try {
      conferenceIds.forEach(async conferenceId => {
        await deleteConference({ clubId, conferenceId, token: authHeader() })
        setConferences(prev => prev.filter(conference => conference.id !== conferenceId))
      })
      enqueueSnackbar(CONFERENCE_MESSAGE.DELETE_SUCCESS, { variant: 'success' })
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? CONFERENCE_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(CONFERENCE_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  }

  const getConferencesState = async () => {
    try {
      const conferences = await getConferences({ clubId, token: authHeader(), date })
      setConferences(conferences)
    } catch (err) {
      if (err instanceof AxiosError) {
        enqueueSnackbar(err.response?.data?.message ?? CONFERENCE_MESSAGE.SERVER_ERROR, { variant: 'error' })
        return
      }
      enqueueSnackbar(CONFERENCE_MESSAGE.UNKNOWN_ERROR, { variant: 'error' })
    }
  }

  useEffect(() => {
    getConferencesState()
  }, [date])

  return (
    <Card>
      <ConferencesTable conferences={conferences} handleDelete={handleDelete} />
    </Card>
  )
}

export default ConferenceList
