import PropTypes from 'prop-types'
import type { FC } from 'react'
import { useState } from 'react'
import { useNavigate, useParams } from 'react-router'

import { Box, Card, CardHeader, Divider, useMediaQuery, useTheme } from '@mui/material'
import type { GridCellParams, GridColDef, GridEventListener, GridRowSelectionModel } from '@mui/x-data-grid'
import { DataGrid } from '@mui/x-data-grid'

import { type Conference } from 'src/models/Conference'

import BulkActions from './BulkActions'

interface ConferencesTableProps {
  className?: string
  conferences: Conference[]
  handleDelete(ids: number[]): void
}

const ConferencesTable: FC<ConferencesTableProps> = ({ conferences, handleDelete }) => {
  const [selectedConferences, setSelectedConferences] = useState<GridRowSelectionModel>([])

  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()
  const { id } = useParams()
  const clubId = Number(id)

  const selectedBulkActions = selectedConferences.length > 0

  const conferenceRows = conferences.map(c => ({
    ...c,
    startAt: new Date(c.startAt).toLocaleDateString(),
    memberSize: c.member.length === 0 ? '-' : `${c.member[0].name} 외 ${c.member.length - 1}명`
  }))

  const columns: GridColDef[] = [
    { field: 'title', headerName: '행사명', width: 250 },
    { field: 'startAt', headerName: '날짜', width: 250 },
    { field: 'semester', headerName: '학기', width: 250 },
    { field: 'memberSize', headerName: '참석 인원', width: 150, sortable: false, filterable: false }
  ]

  const columnsForXS: GridColDef[] = [
    { field: 'title', headerName: '행사명', width: 100 },
    { field: 'memberSize', headerName: '출석 여부', width: 100, sortable: false, filterable: false }
  ]

  // Title 클릭 시 해당 회의 출석체크 페이지로 이동
  const handleClick: GridEventListener<'cellClick'> = params => {
    const selectedConference = conferences.filter(c => c.id === params.id)[0]

    if (params.field === 'title') {
      navigate(`/manage/${clubId}/attendance/${params.id}`, { state: selectedConference })
    }
  }

  return (
    <Card>
      <Box height="55px">
        {selectedBulkActions ? (
          <Box flex={1} p={1}>
            <BulkActions selectedConferences={selectedConferences as number[]} handleDelete={handleDelete} />
          </Box>
        ) : (
          <CardHeader title="행사 목록" />
        )}
      </Box>

      <Divider />
      <Box
        sx={{
          '& .title--bold': {
            fontWeight: 'bold',
            textDecoration: 'underline',
            cursor: 'pointer'
          }
        }}
      >
        <DataGrid
          autoHeight
          rows={conferenceRows}
          columns={matches ? columns : columnsForXS}
          initialState={{
            pagination: { paginationModel: { pageSize: window.innerWidth >= 600 ? 10 : 5 } }
          }}
          pageSizeOptions={[5, 10, 25]}
          checkboxSelection
          onRowSelectionModelChange={newSelectionModel => {
            setSelectedConferences(newSelectionModel)
          }}
          rowSelectionModel={selectedConferences}
          onCellClick={handleClick}
          getCellClassName={(params: GridCellParams<Conference>) => {
            if (params.field === 'title') {
              return 'title--bold'
            } else return ''
          }}
        />
      </Box>
    </Card>
  )
}

ConferencesTable.propTypes = {
  conferences: PropTypes.array.isRequired
}

ConferencesTable.defaultProps = {
  conferences: []
}

export default ConferencesTable
